.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    padding: 1em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }
    &__bg {
        background: rgba(#242623, 0.9);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        border-radius: .625em;
        background: #fff;
        width: 47.9375em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 3.125em;
        display: inline-block;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 1.5em;
        display: flex;
        align-items: center;
        position: absolute;
        top: 1.25em;
        right: 1.25em;
        &:hover{
            path{
                fill: $primary;
            }
        }
        svg {
            width: 100%;
            height: auto;
        }
    }
}

.text{
    color: #000;
    p,
    li{
        font-size: 1em;
        line-height: 150%;
    }
    a{
        text-decoration-line: underline;
    }
    p,ul{
        @include lastM(0 0 .5em);
    }
    ul{
        padding-left: .625em;
        li{
            padding-left: .875em;
            position: relative;
            &:before{
                content: '';
                position: absolute;
                left: 0;
                top: .5625em;
                width: .3125em;
                height: .3125em;
                background: #000;
                border-radius: 50%;
            }
        }
    }
}

.thank-modal{
    .modal__dialog{
        width: 37.5em;
        background-color: $primary;
        color: #fff;
        padding-top: 9.38em;
        padding-bottom: 9.38em;
    }
    .modal__close{
        &:hover{
            path{
                fill: #fff;
            }
        }
    }
    &__icon{
        width: 6.25em;
        display: block;
        margin: 0 auto 3.125em;
    }
    &__content{
        @include grid100gap(.625em);
        text-align: center;
        text-transform: uppercase;
        .text60{
            line-height: 150%;
        }
    }
    &__desc{
        font-size: 2em;
        line-height: 150%;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog{
            padding: 2em 1em;
            margin: 0 auto 1em;
        }
        &__close{
            top: .75em;
            right: .75em;
            width: 1.25em;
        }
    }

    .text{
        p,
        li{
            font-size: .875em;
        }
    }

    .thank-modal {
        .modal__dialog {
            padding-top: 2.5em;
            padding-bottom: 2.5em;
        }
        &__icon{
            width: 3.125em;
            margin-bottom: 1em;
        }
        &__desc{
            font-size: 1em;
        }
    }
}
