.text60{
  font-size: 3.75em;
  line-height: 120%;
}

.text24{
  font-size: 1.5em;
  line-height: 120%;
  letter-spacing: 0.15em;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
  letter-spacing: 0.125em;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.green-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}

.gray-text2{
  color: $gray2;
}

.font2{
  font-family: $font2;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}


@media screen and (max-width: 576px){
  .text60{
    font-size: 1.75em;
  }

  .text24{
    font-size: 1em;
    letter-spacing: 0.1em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text14-tablet{
    font-size: .875em;
  }
}
