.header{
  position: absolute;
  top: 0;
  left: 0;
  padding: 1.875em 0;
  z-index: 5;
  width: 100%;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 7.25em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__phone{
    font-family: $font3;
    font-weight: 800;
    display: flex;
    align-items: center;
    color: #fff;
    .m-icon{
      transform: inherit;
      margin-right: 1.25em;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  .m-icon{
    @include lastM(0 1.25em 0 0);
  }
}

.m-icon{
  width: 3.75em;
  height: 3.75em;
  @include center;
  border-radius: 0.3125em;
  background: #343434;
  flex-shrink: 0;
  &:hover{
    background: $primary;
  }
  img{
    width: 1.875em;
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: .625em 0;
    &__logo{
      width: 1.5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__phone{
      .m-icon{
        display: none;
      }
      .text24{
        font-size: .875em;
        letter-spacing: .0em;
      }
    }
  }

  .m-icon{
    width: 2.125em;
    height: 2.125em;
    img{
      width: 1.125em;
    }
  }

  .socials{
    .m-icon{
      @include lastM(0 .31em 0 0);
    }
  }
}
