@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;

    font-size: calc(980px * (16 / 980));
    @media screen and (min-width: 981px){
        //font-size: calc(100vw * (16 / 980));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
}

.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 1.25em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 980px){
    .m-page{
        font-size: calc(100vw * (12.5 / 768));
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 320));
    }

    .container{
        padding: 0 .9375em;
    }
}
