.fg {
  position: relative;
  margin: 0;
  input {
    font-family: $font1;
    border: 2px solid #fff;
    padding: 0 1.875em 0 5.12em;
    height: 5em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: $black;
    border-radius: 31.25em;
    &::placeholder {
      color: #605A28;
    }
    &._not-correct{
      border-color: red;
    }
  }
  &__icon{
    position: absolute;
    left: 1.875em;
    top: 50%;
    transform: translateY(-50%);
    width: 2em;
  }
}


@media screen and (max-width: 576px){
  .fg{
    input{
      height: 3.75em;
      padding-left: 4.5em;
    }
  }
}
