@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5em;
    height: 5em;
    font-family: $font2;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 120%;
        letter-spacing: 0.1125em;
    }
}

.m-btn-white-outline{
    border-radius: 31.25em;
    border: 2px solid #FFF;
    color: #fff;
    &:hover{
        background-color: #fff;
        color: $primary;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 3.75em;
    }
}
