.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 10.25em 0;
  color: #fff;
  position: relative;
  &__cut{
    display: block;
    width: 100%;
    margin-top: -2px;
    z-index: -1;
  }
  &__top{
    position: relative;
  }
  &__title{
    font-size: 5.625em;
    line-height: 120%;
    text-transform: uppercase;
    font-weight: 700;
    span{
      display: block;
      line-height: inherit;
    }
  }
  &__desc{
    position: absolute;
    top: 1.13em;
    right: 0;
    text-align: right;
    max-width: 12em;
    font-weight: 600;
    text-transform: uppercase;
    span{
      font-size: 1.125em;
      line-height: 150%;
    }
  }
}

.rewards{
  display: flex;
  align-items: center;
  margin-top: 3.125em;
  &__title{
    margin-right: 1.56em;
    text-align: right;
    font-weight: 700;
    text-transform: uppercase;
    span{
      font-size: 1.25em;
      line-height: 120%;
      letter-spacing: 0.125em;
    }
  }
  &__items{
    display: flex;
    flex-direction: row-reverse;
  }
  &__item{
    width: 5em;
    display: block;
    flex-shrink: 0;
    &:not(:last-child){
      margin-left: -.63em;
    }
  }
}

.nav{
  margin-top: 3.125em;
  &__link{
    display: flex;
    align-items: center;
    padding: 2em 3.125em;
    font-weight: 600;
    font-family: $font2;
    border-bottom: 1px solid #fff;
    &:hover{
      path{
        stroke: $primary;
      }
    }
    &:first-child{
      border-top: 1px solid #fff;
    }
    &-num{
      margin-right: 3.125em;
      text-align: center;
      min-width: 5em;
    }
    &-title{
      text-transform: uppercase;
      padding-right: .5em;
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-left: auto;
      flex-shrink: 0;
    }
  }
}

.main-form{
  position: absolute;
  bottom: -4.12em;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: calc(1200px - 2.5em);
  padding: 3.125em 3.125em 1.25em;
  background-color: $primary;
  border-radius: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 20.625em) 20.625em;
  }
  &__bottom{
    display: flex;
    justify-content: center;
    margin-top: 1em;
  }
  &__conf{
    font-size: .75em;
    line-height: 120%;
    font-weight: 600;
    letter-spacing: 0.075em;
    text-decoration-line: underline;
    text-transform: uppercase;
    color: #AEF1AD;
    &:hover{
      color: #fff;
    }
  }
}

.services{
  padding: 3.12em 0 7.44em;
  &__grid{
    display: grid;
    grid-template-columns: 50% 50%;
    width: calc(100% + 1px);
    margin-top: 1.875em;
  }
  @media screen and (min-width: 577px){
    .service-card{
      margin-top: -1px;
      &:nth-child(1),
      &:nth-child(2){
        margin-top: 0;
      }
      &:nth-child(even){
        margin-left: -1px;
      }
    }
  }
}

.info-block{
  text-align: center;
  text-transform: uppercase;
  @include grid100gap(.3125em);
  .text18{
    color: #D0D4BD;
  }
}

.service-card{
  border-radius: 0.625em;
  border: 1px solid #D0D4BD;
  padding: 3.125em;
  display: block;
  position: relative;
  &:hover{
    border-color: $primary;
    z-index: 2;
    .service-card__arrow{
      path{
        stroke: $primary;
      }
    }
  }
  &__icon{
    height: 4.5em;
    img{
      height: 100%;
      display: block;
    }
  }
  &__content{
    margin-top: 3.125em;
    @include grid100gap(.625em);
  }
  &__arrow{
    position: absolute;
    top: 1.875em;
    right: 1.875em;
    width: 1.0625em;
    height: auto;
  }
  .text18{
    text-transform: uppercase;
  }
}

.lines{
  z-index: 2;
  pointer-events: none;
  margin-top: -13.25em;
  height: 19.375em;
  position: relative;
  overflow: hidden;
  img{
    height: 100%;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.portfolio{
  background-image: url(../img/portfolio-bg.png);
  background-size: cover;
  background-position: center;
  padding: 4.625em 0;
  color: #fff;
  margin-top: -6.25em;
  .info-block{
    .text18{
      color: #4AC32B;
    }
  }
}

.portfolio-slider{
  margin-top: 1.875em;
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-button{
      width: 2.75em;
      height: 2.75em;
      background-color: $primary;
      border-radius: 0.3125em;
      @include center;
      img{
        width: 1.125em;
        display: block;
      }
      &-next{
        right: 4.56em;
      }
      &-prev{
        left: 4.56em;
      }
    }
    &-pagination{
      font-size: 1em;
      font-weight: 700;
      line-height: 120%;
      color: #635D4A;
      bottom: 100%;
      width: unset;
      right: 0;
      left: unset;
      margin-bottom: 1.875em;
      font-family: $font2;
    }
  }
}

.portfolio-card{
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    margin-top: 1.25em;
    text-align: center;
    text-transform: uppercase;
    @include grid100gap(.3125em);
    .text14{
      color: #98958C;
      letter-spacing: 0.0875em;
    }
  }
}

.about{
  margin-top: -4.375em;
  position: relative;
  z-index: 2;
  padding-bottom: 2.69em;
  &__inner{
    display: grid;
    grid-template-columns: 24.58394em calc(100% - 24.58394em - 2.97em);
    justify-content: space-between;
    gap: 2em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      transform: rotate(-4.97deg);
    }
  }
  &__content{
    padding-top: 9.87em;
  }
  .info-block{
    text-align: left;
  }
  &__text{
    margin-top: 1.875em;
    @include grid100gap(1em);
    p{
      font-size: 1.125em;
      line-height: 150%;
    }
  }
  &__link{
    margin-top: 2.44em;
    display: flex;
    align-items: center;
    padding: 1em 3.125em;
    height: 6.25em;
    border-radius: 0.625em;
    background: #4AC32B;
    color: #FFF;
    font-weight: 600;
    text-transform: uppercase;
    &:hover{
      color: #fff;
      background: darken(#4AC32B,10%);
    }
    &-logo{
      width: 1.84em;
      flex-shrink: 0;
      margin-right: 1.875em;
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-left: auto;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 3.44em 0 2.63em;
    .container{
      padding: 0 .5em;
    }
    &__title{
      font-size: 1.75em;
      text-align: center;
    }
    &__desc{
      position: static;
      margin: .625em auto 0;
      text-align: center;
      span{
        font-size: .75em;
      }
    }
  }

  .rewards{
    flex-direction: column;
    margin-top: 1.25em;
    &__title{
      text-align: center;
      margin: 0 0 .75em;
      br{
        display: none;
      }
      span{
        font-size: .875em;
        letter-spacing: 0.0875em;
      }
    }
    &__items{
      justify-content: center;
    }
    &__item{
      width: 2.5em;
    }
  }

  .nav{
    margin-top: 1.25em;
    margin-left: -.5em;
    width: calc(100% + 1em);
  }

  .nav{
    &__link{
      padding: 1.12em .9375em;
      &-num{
        min-width: 3.75em;
        margin-right: .625em;
      }
      svg{
        width: 1.25em;
      }
    }
  }

  .main-form{
    padding-left: .9375em;
    padding-right: .9375em;
    position: static;
    transform: unset;
    margin-top: 1.25em;
    &__grid{
      grid-template-columns: 100%;
      gap: .31em;
    }
  }

  .services{
    padding: 1.875em 0;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    .service-card{
      margin-top: -1px;
      &:first-child{
        margin-top: 0;
      }
    }
  }

  .service-card{
    padding: 1.25em;
    &__arrow{
      display: none;
    }
    &__icon{
      height: 3.125em;
    }
    &__content{
      margin-top: 1.25em;
    }
  }

  .lines{
    height: 8.12131em;
    margin-top: -4.62em;
  }

  .portfolio{
    margin-top: -3.25em;
    padding: 2.56em 0 3.125em;
  }

  .portfolio-slider{
    .swiper{
      &-pagination{
        display: none;
      }
      &-button{
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
  }

  .portfolio-card{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__content{
      margin-top: .625em;
    }
  }

  .about{
    margin-top: -3.6em;
    padding-bottom: 1.25em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      padding: 0 1.375em;
    }
    &__content{
      padding: 0;
    }
    &__text{
      margin-top: 1.25em;
      p{
        font-size: 1em;
      }
    }
    &__link{
      margin-top: 1.25em;
      height: 5em;
      padding: 0 1.25em;
    }
  }
}
