@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.animated{

}
.main__title{
  .animated{
    transition: all .6s;
    transform: translateX(-2em);
    opacity: 0;
    &--active{
      opacity: 1;
      transform: translateX(0);
    }
    &:nth-child(2){
      transition-delay: .2s;
    }
    &:nth-child(3){
      transition-delay: .4s;
    }
  }
}

.about__img,
.portfolio-slider,
.rewards{
  &.animated{
    transition: all .6s;
    opacity: 0;
    transition-delay: .2s;
    &--active{
      opacity: 1;
    }
  }
}

.animated,
.nav__link{
  &.animated{
    transition: transform .4s, opacity .4s, color .3s;
    opacity: 0;
    transform: translateY(1em);
    transition-delay: .2s;
    &--active{
      opacity: 1;
      transform: translateY(0);
    }
    &:nth-child(2){
      transition-delay: .2s;
    }
    &:nth-child(3){
      transition-delay: .4s;
    }

  }
}

.nav__link{
  &.animated{
    &:nth-child(1),
    &:nth-child(2){
      transition-delay: 0s;
    }
    &:nth-child(3),
    &:nth-child(4){
      transition-delay: .2s;
    }
    &:nth-child(5),
    &:nth-child(6){
      transition-delay: .4s;
    }
  }
}
